@import "main";

// container

body{
    font-family: 'Montserrat', sans-serif;
    margin: 0;
}

#container{
    font-size: 20px;
    margin: 0 auto;
    display: grid;
    grid-template-rows: auto 1fr auto;
    width: 1024px;
}

// header 

header.top-header {
    display: grid;
    grid-template-columns: 200px 1fr;
    place-items: self-end;
    background: #a496c430;
}

.site-title {
    margin: 0;
    padding: 10px 20px;
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 2px;
    font-family: 'Limelight', cursive;
}

h1 {
    color: #9e9e9e;
    letter-spacing: 5px;
    text-align: center;
}

h3 {
    color: black;
    text-align: center;
}

li h2 {
    text-align: center;
}

// main 

.main {
    min-height: 60vh;
    padding: 20px;
    padding-bottom: 0;
    display: grid;
    grid-template-columns: 70% 1fr;
    grid-gap: 30px;
}

.main ul {
    display: grid;
    grid-template-columns: 1fr 1fr;
    list-style-type: none;
    grid-gap: 20px;
}

img {
    width: 200px;
}

a {
    text-decoration: none;
}

p {
    font-size: 23px;
    line-height: 38px;
    text-align: justify;
}

.main_aside ul {
    display: block;
    list-style-type: decimal-leading-zero;
}

::marker {
    color: #673ab7;
}

.main_aside li {
    padding: 10px 0;
    font-size: 1rem;
}

// navigation 

nav {
    background: #9479e354;
    padding: 3px;
}

nav ul {
    display: flex;
    justify-content: flex-end;
    list-style: none;
}

nav li {
    padding: 0 15px;
}

nav a {
    color: #3d05a1;
    font-weight: bold;
    letter-spacing: 2px;
}

nav a:hover{
    color: black;
}

// footer 

footer h2 {
    padding: 20px;
    margin: 0;
}

footer {
    background: #9479e354;
}